  padding-left: 20px;
  padding-right: 20px;
}

main {
  font-family: sans-serif;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 1rem;
}

.home-card h1 {
  font-size: 20px;
