  overflow: auto;
  border-radius: 20px;
  outline: none;
  padding: 10px 20px 20px;
  width: 40%;
  line-height: 40px;
}

.proceed {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 10px;
}

.myoverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
