.loan-options{
    width: 150%;
    font-family: Roboto;
    font-size: 14px;
    box-shadow: 0px 0px 5px#888888;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    border-radius: 6px;
    margin: 1% 1% 1% -85px;
    padding: 0 2rem 0 2rem;
}

.loan-options h1 {
    font-size: x-large;
    font-weight: bolder;
    padding: 20px 0px 10px 0px;
}

.loan-options span{
    padding: 0rem 0rem 2rem 0rem;
}