@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap");

.calculator {
    width: 90vw;
    font-family: Roboto;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    position: absolute;
    right: 1%;
    margin: 1rem;
    top: 490px;
}

.calculator h3 {
    background-color: #010f3e;
    color: white;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 2rem;
}

.calculator-values {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    text-align: right;
    font-size: 14px;
    padding: 20px;
    line-height: 45px;
    background-color: white;
}

.value {
    display: flex;
    justify-content: space-between;
}

.value span{
    font-size: small;
}

.value input {
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid gray;
    text-align: end;
    max-width: 4rem;
}


.value stringsFields {
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid gray;
    text-align: end;
}

.button-apply p {
    margin-top: 20px;
    background-color: #010f3e;
    width: wrap;
    color: white;
    border-radius: 5px;
    font-size: large;
    text-align: center;
}


@media (min-width: 992px) {

    .calculator {
        width: 30%;
        box-shadow: 0px 0px 5px #888888;
        right: 3%;
        top: 280px;
        margin: 9rem 0;

    }

    .calculator h3 {
        height: 4rem;
    }

    .button-apply p:active {
        margin-top: 20px;
        background-color: white;
        color: #010f3e;
        border-radius: 5px;
        border: solid 1px #010f3e;
        font-size: large;
        text-align: center;
    }

    .value select {
        border: 0;
        outline: 0;
        background: transparent;
        border-bottom: 1px solid gray;
        text-align: end;
        min-width: 120pt;
    }

    .value span{
        font-size: medium;
    }
}
