header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;
}

main {
  font-family: sans-serif;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 1rem;
}

.home-card h1 {
  font-size: 20px;
  width: 60vw;
  margin: 18px;
  color: #0c2340;
  font-family: VAG Rounded Std Bold;
}

main h3 {
  font-weight: 200;

  margin: 20px;
  color: #0c2340;
}

.button-calculate {
  margin-left: 18px;
  background-color: #010f3e;
  width: max-content;
  padding: 14px;
  color: white;
  font-size: 14px;
  border-radius: 5px;
  font-family: Myriad Pro;
}

.modal {
  font-family: Roboto;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* border: 1px solid #ccc; */
  background: #fff;
  overflow: auto;
  border-radius: 20px;
  outline: none;
  padding: 10px 20px 20px;
  width: 40%;
  line-height: 40px;
}

.proceed {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 10px;
}

.myoverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.75);
}

.close {
  text-align: end;
}

.membership-modal input {
  border: 0px;
  outline: none;
  border-bottom: solid 1px lightgray;
  font-size: 18px;
  padding: 10px;
  /* margin: 10px; */
}

.otp-modal input {
  border: 0px;
  outline: none;
  border-bottom: solid 1px lightgray;
  font-size: 18px;
  padding: 10px;
  /* margin: 10px; */
}

.center-flex {
  justify-content: center;
}

.radio-options span {
  margin: 10px;
}

.login-modal {
  margin-top: 40px;
}

.description {
  display: flex;
  width: 300px;
  justify-content: space-between;
  margin-left: 20px;
  color: #0c2340;
  font-family: "VAG Rounded Thin";
}

.top-border {
  border-top: solid #0c2340 5px;
  padding-top: 10px;
}

.contact {
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  text-align: end;
  margin-right: -3rem;
}

.button-calculate:hover {
  cursor: pointer;
}

.logo {
  margin-left: -4rem;
}

.dialogContent {
  display: flex;
  flex-flow: column;
}

.dialogContent p {
  font-size: small;
}

.dialogContent div {
  align-self: start;
  padding-left: 1rem;
}

.otp-modal {
  padding: 2px;
}

