/*
About Us Page

*/
.container {
    width: 50%;
    font-family: Roboto;
    font-size: 14px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 3% auto;
}

.container-about-us {
    height: 0;
}

.container .subtitle {
    display: inherit;
    flex-direction: row;
    align-items: baseline;
    align-self: flex-start;

}

.container .subtitle h1 {
    margin-top: -3px;

}

.container h1 {
    font-family: Myriad Pro;
    font-size: x-large;
    font-weight: bolder;
    display: flex;
    flex-direction: row;
    alignment-baseline: bottom;
    align-self: flex-start;
    margin-left: 10px;
    margin-top: 10px;

}

.box {
    width: 21px;
    height: 21px;
    background-color: #010f3e;
    align-self: flex-start;
}

.h1 {
    padding-bottom: -20px;
}

.list_items {
    display: list-item;
    list-style-type: disc;
    list-style-position: inside;
    align-self: flex-start;
    padding-left: 50px;
}

.container span {
    align-self: flex-start;
    margin: 10px;
    font-size: 12pt;
}

/*
===============
Contact Page
===============
*/

.container-contact-us{
    height: 10vw;
}

.contact-page {
    display: grid;
    place-items: center;
    padding: 5rem 0;
}

.contact-form {
    background: white;
    border-radius: 0.25rem;
    text-align: center;
    box-shadow: #e1dede;
    transition: all 0.3s linear;
    width: 90vw;
    max-width: 35rem;
}

.contact-form:hover {
    box-shadow: #313030;
}

.contact-form h3 {
    color: hsl(210, 22%, 49%);
    font-size: large;
    text-transform: uppercase;

}

.form-group {
    padding: 1rem 1.5rem;
}

.form-control {
    display: block;
    width: 100%;
    padding: 0.75rem 1rem;
    border: none;
    margin-bottom: 1.25rem;
    background: #b5c2ef;
    border-radius: 0.25rem;
    text-transform: uppercase;
    letter-spacing: 0.2rem;
}

.form-control::placeholder {
    font-family: "Roboto", sans-serif;
    color: lightslategray;
    text-transform: uppercase;
    letter-spacing: 0.2rem;
}

.submit-btn {
    display: block;
    width: 100%;
    padding: 1rem;
    background-color: #010f3e;
    color: white;
    font-size: large;
}

.contact-form .subtitle {
    display: flex;
    flex-direction: row;
    align-items: baseline;
    align-self: flex-start;
    margin-bottom: 10px;
}

/*
Faqs
*/

.faqs-image {
    margin-top: 2%;
    margin-bottom: -29%;
}

.about-info {
    padding: 0 20px;
}

/**
*policies
*
*/

.policies {
    padding: 0 2rem;
    margin-top: 2rem;
    margin-bottom: -10rem;
}

.policies h1 {
    margin-bottom: 0.25rem;
}

.policies h4 {
    margin-bottom: 0.35rem;
    font-style: normal;
}

.policies ul {
    margin-bottom: 0.35rem;
}

@media (min-width: 992px) {
    .policies {
        padding: 0 13rem;
    }
}

/*
TnCs
*/
.tncs {
    margin-bottom: -15rem;
}

.pagec {
    display: flex;
    margin: 2rem;
    align-content: space-between;
}
