.loan-details {
  width: 90%;
  font-family: Roboto;
  font-size: 14px;
  box-shadow: 0px 0px 5px#888888;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin: 2%;
  border-radius: 6px;
}

.banner_text{
  display: none;
  font-family: Myriad Pro;
}

.site-root{
  height: 500vw;
}

.loan-options{
  margin-top: 400px;
}


@media (min-width: 992px) {

  .loan-details-value span {
    font-size: 14px;
  }

  .site-root{
    height: auto;
  }

  .loan-details h3 {
    font-family: Roboto;
    font-size: x-large;
    font-weight: bolder;
    padding-left: 5%;
    padding-top: 5%;
  }

  .loan-details-values {
    padding-left: 5%;
    padding-right: 5%;
    padding-bottom: 5%;
  }

  .loan-details-value {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    outline: none;
    padding: 1%;
    font-size: 18px;
  }

  .loan-details-value input:focus {
    border: 0px;
    outline: none;
  }

  .loan-details-value input {
    border: 0px;
    line-height: 25px;
    font-size: 14px;
    margin-left: 10px;
  }

  .button-proceed p {
    margin-top: 20px;
    background-color: #010f3e;
    width: wrap;
    color: white;
    border-radius: 5px;
    padding: 10px;
    font-size: large;
    text-align: center;
    align-self: flex-end;
  }

  .button-proceed p:active {
    margin-top: 20px;
    background-color: white;
    width: wrap;
    border-radius: 5px;
    padding: 10px;
    text-align: center;
    color: #010f3e;
    font-size: large;
    border: #010f3e solid 1px;
  }

  .employee-details {
    width: 90%;
    font-family: Roboto;
    box-shadow: 0px 0px 10px#888888;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    margin: 2%;
    padding-bottom: 5%;
  }

  .employee-details h3 {
    font-family: Roboto;
    font-size: x-large;
    font-weight: bolder;
    padding-left: 5%;
    padding-top: 5%;
    margin-bottom: 5%;
  }
  .employee-details p {
    font-family: Roboto;
    font-size: medium;
    padding-left: 5%;
    margin-bottom: 5%;
  }
  .employee-choice {
    padding: 5%;
  }

  .personal-details {
    width: 90%;
    font-family: Roboto;
    box-shadow: 0px 0px 10px#888888;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    margin: 2%;
    padding-bottom: 5%;
  }

  .personal-details h3 {
    font-size: x-large;
    padding: 5%;
  }

  .personal-details div {
    display: flex;
    justify-content: space-between;
  }

  .personal-details-values input {
    width: 100%;
    margin-left: 4%;
    margin-right: 4%;
    margin-top: 2%;
    font-size: 14px;
    padding: 5px;
    border: none;
    border-bottom: solid 1px lightgray;
  }

  .personal-details-values input:focus {
    outline: none;
  }

  .loan-buttons {
    display: flex;
    justify-content: flex-end;
    box-sizing: border-box;
  }

  .button-space {
    padding: 2px;
  }

  .button-submit {
    margin-top: 20px;
    background-color: #010f3e;
    width: wrap;
    color: white;
    border-radius: 5px;
    padding: 10px;
    font-size: large;
    text-align: center;
    /* align-self:; */
  }

  /* .button-submit p {
    text-align: center;
  } */
  .buttons {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }

  #arrowAnim {
    width: 90%;
    font-family: Roboto;
    font-size: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5%;
  }

  .arrow {
    width: 3vw;
    height: 3vw;
    border: 2.5vw solid;
    border-color: #010f3e transparent transparent #010f3e;
    transform: rotate(135deg);
  }


  .arrowSliding {
    position: absolute;
    -webkit-animation: slide 4s linear infinite;
    animation: slide 4s linear infinite;
  }

  .delay1 {
    -webkit-animation-delay: 3s;
    animation-delay: 3s;
  }
  .delay2 {
    -webkit-animation-delay: 2s;
    animation-delay: 2s;
  }
  .delay3 {
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
  }

  @-webkit-keyframes slide {
    0% { opacity:0; transform: translateX(-15vw); }
    20% { opacity:1; transform: translateX(-9vw); }
    80% { opacity:1; transform: translateX(9vw); }
    100% { opacity:0; transform: translateX(15vw); }
  }
  @keyframes slide {
    0% { opacity:0; transform: translateX(-15vw); }
    20% { opacity:1; transform: translateX(-9vw); }
    80% { opacity:1; transform: translateX(9vw); }
    100% { opacity:0; transform: translateX(15vw); }
  }

  .banner_text{
    display: block;
    font-size: 4rem;
  }

}